<template>
    <div>
        <ul>
            <ToDoItem v-for="(todo, i) of todos"
            v-bind:todo="todo"
            v-bind:index="i"
            v-on:remove-todo="removeTodo" />
        </ul>
    </div>
</template>

<script>
    import ToDoItem from "@/components/ToDoItem";
    
    export default {
        props: ["todos"],
         components: {
            ToDoItem
  },
  methods: {
      removeTodo(id) {
        this.$emit("remove-todo", id)
      }
  }
};
</script>

<style scoped>
    ul {
        list-style-type: 0;
        padding: 5px;
        margin: 0;
     
    }
</style>
  
